import { useTranslation } from "react-i18next";
import { useCallback, useState } from "react";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import {
  useAcademicSubjectStudentExport,
  useAddStudentInTheoryClasses,
  useOneTheoryClasses,
} from "../../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import CreateButton from "../../../../Ui/Button/CreateButton";
import InstituteTheoryClassDetailCard from "./InstituteTheoryClassDetailCard";
import InstituteDepartmentAddStudent from "../InstituteDepartmentAddStudent/InstituteDepartmentAddStudent";
import InstituteTheoryClassDetailReshuffle from "./InstituteTheoryClassDetailReshuffle/InstituteTheoryClassDetailReshuffle";
import { useLocation } from "react-router-dom";
import InstituteDepartmentAddDseStudent from "../InstituteDepartmentAddStudent/InstituteDepartmentAddDseStudent";
import { cdnUrl } from "../../../../Services/UrlConfig/BaseUrl";

const InstituteTheoryClassDetail = ({ clsId, openAs }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [search, setSearch] = useState("");
  const [add, setAdd] = useState(false);
  const [dse, setDse] = useState(false);
  const [reshuffle, setReshuffle] = useState(false);
  const [disabled, setDisabled] = useState(false);
  const [addStudentInTheoryClasses] = useAddStudentInTheoryClasses();

  const [academicSubjectStudentExport] = useAcademicSubjectStudentExport();
  const { oneTheoryClasses, oneTheoryClassesRefetch, oneTheoryClassesLoading } =
    useOneTheoryClasses({
      data: {
        sid: getQuery.state.subId,
        search: search,
      },
      skip: !getQuery.state.subId,
    });

  const onDebounce = useCallback(
    customDebounce((val) => {
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  const onDseToggle = () => {
    setDse((pre) => !pre);
  };

  const onReshuffleToggle = () => {
    setReshuffle((pre) => !pre);
  };
  const onAddStudent = (data) => {
    if (data?.length > 0 && getQuery.state.subId) {
      setDisabled((pre) => !pre);
      addStudentInTheoryClasses({
        sid: getQuery.state.subId,
        bodyParameter: {
          students: data,
        },
      })
        .then(() => {
          oneTheoryClassesRefetch();
          setDisabled((pre) => !pre);
          onToggle();
        })
        .catch({});
    }
  };
  const onExportData = () => {
    if (getQuery.state.subId) {
      setDisabled((pre) => !pre);
      academicSubjectStudentExport({
        sid: getQuery.state.subId,
      })
        .then((res) => {
          if (res?.data?.set) {
            setDisabled((pre) => !pre);
            const newWindow = window.open(
              `${cdnUrl}/${res?.data?.set}`,
              "_blank",
              "noopener,noreferrer"
            );
            if (newWindow) newWindow.opener = null;
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };
  // console.info("oneTheoryClasses");
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{getQuery.state?.subjectName ?? ""}</h6>
          <div className={style.fm_search}>
            <CreateButton
              label={t("resuffle")}
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
                minWidth: "fit-content",
              }}
              onAction={onReshuffleToggle}
            />
            <CreateButton
              label={t("add_student")}
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
                minWidth: "fit-content",
              }}
              onAction={onToggle}
            />
            <CreateButton
              label={t("add_dse_student")}
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
                minWidth: "fit-content",
              }}
              onAction={onDseToggle}
            />
            <img
              src={`${assestsUserFinanceUrl}/export.svg`}
              alt="icon"
              title="List Export"
              onClick={onExportData}
            />
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0",
        }}
      >
        <div className={style.fm_search_full}>
          <h6
            className={style.fm_title_heading}
            style={{
              width: "-webkit-fill-available",
            }}
          >
            {t("total_student")}

            {oneTheoryClasses?.theory_students?.length ?? 0}
          </h6>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
              />
            </div>
          </section>
        </div>
      </UserMemberContentWrapper>

      <BorderBottom />

      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0.3rem",
          paddingBottom: "1rem",
        }}
      >
        {oneTheoryClasses?.theory_students?.map((student) => (
          <InstituteTheoryClassDetailCard
            student={student}
            key={student?._id}
            openAs={openAs}
            subjectId={getQuery.state.subId}
            onRefetch={oneTheoryClassesRefetch}
          />
        ))}
        {oneTheoryClassesLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          oneTheoryClasses?.theory_students?.length === 0 && (
            <EmptyMessage title="No student found related this search." />
          )
        ) : (
          oneTheoryClasses?.theory_students?.length === 0 && (
            <EmptyMessage title="No student." />
          )
        )}
      </UserMemberContentWrapper>

      {add && (
        <InstituteDepartmentAddStudent
          onClose={onToggle}
          clsId={clsId}
          onAdd={onAddStudent}
          academicDepartmentId={getQuery.state?.departmentId}
        />
      )}
      {dse && (
        <InstituteDepartmentAddDseStudent
          onClose={onDseToggle}
          clsId={clsId}
          onAdd={onAddStudent}
          academicDepartmentId={getQuery.state?.departmentId}
        />
      )}
      {reshuffle && (
        <InstituteTheoryClassDetailReshuffle
          onClose={onReshuffleToggle}
          clsId={clsId}
          onRefetch={oneTheoryClassesRefetch}
        />
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default InstituteTheoryClassDetail;
