import { useState } from "react";
import {
  assestsUserFinanceUrl,
  assestsFeedUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import InstituteTheoryClassDetailCardMenu from "./InstituteTheoryClassDetailCardMenu";
import style from "../../../InstituteFeedback/InstituteFeedback.module.css";
import { imageShowUrl } from "../../../../Services/UrlConfig/BaseUrl";
import { useTranslation } from "react-i18next";
import { Link, useParams } from "react-router-dom";
const InstituteTheoryClassDetailCard = ({
  student,
  openAs,
  onRefetch,
  subjectId,
}) => {
  const [open, setOpen] = useState(false);
  const params = useParams();

  const { t } = useTranslation();

  const onToggle = (e) => {
    if (e) e.preventDefault();
    setOpen((pre) => !pre);
  };
  return (
    <>
      <Link
        to={`/q/${params.username}/student/profile`}
        state={{
          sid: student?._id,
          // viewAs: "SUBJECT_TEACHER",
          viewAs: openAs ? "SUBJECT_TEACHER" : "INSTITUTE",
        }}
      >
        <div className={style.iadcc_card_hover}>
          <div className={style.sf_card_contatiner}>
            <img
              src={
                student?.studentProfilePhoto
                  ? `${imageShowUrl}/${student.studentProfilePhoto}`
                  : `${assestsFeedUrl}/feed-user-avatar.svg`
              }
              loading="lazy"
              alt="avatar"
              style={{
                width: "2.5rem",
                height: "2.5rem",
              }}
            />

            <div className={style.sf_card_contatiner_inner}>
              <h6>
                {`${student?.studentFirstName ?? ""} ${
                  student?.studentMiddleName ?? ""
                } ${student?.studentLastName ?? ""}`}
              </h6>
              <p>
                {t("gr_number")} {": "}
                {student?.studentGRNO ?? ""}
              </p>
            </div>
          </div>
          <img
            src={`${assestsUserFinanceUrl}/menu.svg`}
            alt="menu icon"
            onClick={onToggle}
          />
        </div>
      </Link>
      {open && (
        <InstituteTheoryClassDetailCardMenu
          onClose={onToggle}
          menuData={student}
          onRefetch={onRefetch}
          subjectId={subjectId}
        />
      )}
    </>
  );
};

export default InstituteTheoryClassDetailCard;
