import {
  assestsFeedUrl,
  assestsUserAdmissionUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../InstituteFeedback/InstituteFeedback.module.css";
import { imageShowUrl } from "../../../../Services/UrlConfig/BaseUrl";
import { useTranslation } from "react-i18next";

const InstituteDepartmentAddStudentCard = ({
  student,
  onSelect,
  selectedId,
}) => {
  const { t } = useTranslation();

  const onSelectStudent = () => {
    onSelect(student);
  };
  return (
    <div
      className={style.sf_card_hover}
      style={{
        justifyContent: "flex-start",
        gap: "1rem",
        alignItems: "center",
      }}
      onClick={onSelectStudent}
    >
      <div className={style.sf_card_contatiner_inner}>
        <h6>{student?.studentROLLNO ?? ""}</h6>
      </div>

      {selectedId?.includes(student?._id) ? (
        <img
          src={`${assestsUserAdmissionUrl}/check.svg`}
          alt="icon"
          style={{
            height: "1.3rem",
            borderRadius: "0",
            cursor: "pointer",
          }}
        />
      ) : (
        <img
          style={{
            height: "1.3rem",
            borderRadius: "0",
            cursor: "pointer",
          }}
          src={`${assestsUserAdmissionUrl}/uncheck.svg`}
          alt="icon"
        />
      )}
      <div className={style.sf_card_contatiner}>
        <img
          src={
            student?.studentProfilePhoto
              ? `${imageShowUrl}/${student.studentProfilePhoto}`
              : `${assestsFeedUrl}/feed-user-avatar.svg`
          }
          loading="lazy"
          alt="avatar"
        />

        <div className={style.sf_card_contatiner_inner}>
          <h6>
            {`${student?.studentFirstName ?? ""} ${
              student?.studentMiddleName ?? ""
            } ${student?.studentLastName ?? ""}`}
          </h6>
          <p>
            {t("gr_number")} {": "}
            {student?.studentGRNO ?? ""}
          </p>
        </div>
      </div>
    </div>
  );
};

export default InstituteDepartmentAddStudentCard;
