import { useState } from "react";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import { useRemoveStudentInTheoryClasses } from "../../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";

const InstituteTheoryClassDetailCardMenu = ({
  subjectId,
  onClose,
  menuData,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [removeStudentInTheoryClasses] = useRemoveStudentInTheoryClasses();

  const onDelete = () => {
    if (menuData?._id && subjectId) {
      setDisabled((pre) => !pre);
      removeStudentInTheoryClasses({
        sid: subjectId,
        bodyParameter: {
          students: [menuData?._id],
        },
      }).then(() => {
        onRefetch();
        setDisabled((pre) => !pre);
        onClose();
      });
    }
  };
  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        <h6 onClick={onDelete}>{t("remove")}</h6>
      </ModalMenu>
    </>
  );
};

export default InstituteTheoryClassDetailCardMenu;
