import style from "../../../../UserStaffMember/PlateformUser/PlateformUser.module.css";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import { useCallback, useState } from "react";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useClassMasterAllStudent } from "../../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import InstituteDepartmentAddStudentCard from "./InstituteDepartmentAddStudentCard";
const InstituteDepartmentAddStudent = ({
  title,
  onClose,
  clsId,
  onAdd,
  academicDepartmentId,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [selectedId, setSelectedId] = useState([]);
  const [isAll, setIsAll] = useState(false);
  const { classMasterAllStudent, classMasterAllStudentLoading } =
    useClassMasterAllStudent({
      data: {
        cid: clsId,
        page: 1,
        limit: 1000,
        search: search,
        did: academicDepartmentId,
      },
      skip: !clsId,
    });

  const onDebounce = useCallback(
    customDebounce((val) => {
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setSearch("");
    }
  };

  const onAllSelect = () => {
    let arr = [];
    for (let ft of classMasterAllStudent?.list) {
      arr.push(ft?._id);
    }
    setSelectedId(arr);
    setIsAll(true);
  };
  const onAllRemove = () => {
    setSelectedId([]);
    setIsAll(false);
  };
  const onSelect = (value) => {
    if (selectedId?.includes(value?._id)) {
      setSelectedId((prev) => prev?.filter((dt) => dt !== value?._id));
    } else {
      setSelectedId((prev) => [...prev, value?._id]);
    }
  };

  const onAddStudent = () => {
    onAdd(selectedId);
  };

  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t(title ?? "add_students")}</h6>
            </div>
            <div className={style.modal_container_btn_container}>
              {isAll ? (
                <CreateButton
                  label={t("remove_all")}
                  count={selectedId?.length}
                  onAction={onAllRemove}
                  customStyle={{
                    margin: "0",
                    padding: "0.3rem 0.5rem",
                  }}
                />
              ) : (
                <CreateButton
                  label={t("select_all")}
                  count={selectedId?.length}
                  onAction={onAllSelect}
                  customStyle={{
                    margin: "0",
                    padding: "0.3rem 0.5rem",
                  }}
                />
              )}
              <CreateButton
                label={t("add")}
                onAction={onAddStudent}
                customStyle={{
                  margin: "0",
                  padding: "0.3rem 0.5rem",
                }}
              />
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "1rem",
          }}
        >
          <div
            className={style.assign_search_container}
            style={{
              marginBottom: "1rem",
            }}
          >
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              onChange={onSearchEvent}
              style={{
                width: "100%",
              }}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
            />
          </div>

          {classMasterAllStudent?.list?.map((student) => (
            <InstituteDepartmentAddStudentCard
              student={student}
              onSelect={onSelect}
              selectedId={selectedId}
              key={student?._id}
            />
          ))}
          {classMasterAllStudentLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            classMasterAllStudent?.list?.length === 0 && (
              <EmptyMessage
                title="No student found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            classMasterAllStudent?.list?.length === 0 && (
              <EmptyMessage
                title="No student."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};
export default InstituteDepartmentAddStudent;
