import QvipleLoader from "../../Loader/QvipleLoader";
import Modal from "./Modal";
import style from "./Modal.module.css";
import BorderBottom from "../Border/BorderBottom";
const ModalMenu = ({
  onClose,
  disabled,
  heading,
  customStyle,
  customBackdropStyle,
  customOverlayStyle,
  children,
}) => {
  const onCloseEvent = () => {
    if (disabled) {
      return null;
    } else {
      onClose();
    }
  };
  return (
    <Modal
      onClose={onCloseEvent}
      customBackdropStyle={customBackdropStyle}
      customOverlayStyle={customOverlayStyle}
    >
      <div className={style.modal_menu_card} style={customStyle}>
        {heading ? (
          <>
            <h5>{heading}</h5>
            <BorderBottom />
          </>
        ) : null}
        {children}
        {disabled && <QvipleLoader />}
      </div>
    </Modal>
  );
};

export default ModalMenu;
