import style from "../../../../../UserStaffMember/PlateformUser/PlateformUser.module.css";
import Modal from "../../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../../Ui/Border/BorderBottom";
import { assestsUserFinanceUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import { useEffect, useRef, useState } from "react";
import Loading from "../../../../../Loader/Loading";
import EmptyMessage from "../../../../../Utils/EmptyMessage/EmptyMessage";
import CreateButton from "../../../../../Ui/Button/CreateButton";
import { useClassMasterAllStudent } from "../../../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import InstituteTheoryClassDetailReshuffleCard from "./InstituteTheoryClassDetailReshuffleCard";
import QvipleLoader from "../../../../../Loader/QvipleLoader";

const InstituteTheoryClassDetailReshuffle = ({ clsId, onClose, onRefetch }) => {
  const { t } = useTranslation();

  const [list, setList] = useState([]);
  const [disabled, setDisabled] = useState(false);
  const dragItem = useRef();
  const dragOverItem = useRef();

  const { classMasterAllStudent, classMasterAllStudentLoading } =
    useClassMasterAllStudent({
      data: {
        cid: clsId,
        page: 1,
        limit: 1000,
        search: "",
      },
      skip: !clsId,
    });
  useEffect(() => {
    if (classMasterAllStudent?.list) {
      setList(classMasterAllStudent?.list);
    }
  }, [classMasterAllStudent?.list]);

  const onDragEventStart = (e, position) => {
    dragItem.current = position;
  };

  const onDragEventEnter = (e, position) => {
    dragOverItem.current = position;
  };
  const onDropItem = (e) => {
    const listCopy = [...list];
    const dragItemContentCls = listCopy[dragItem.current];
    listCopy.splice(dragItem.current, 1);
    listCopy.splice(dragOverItem.current, 0, dragItemContentCls);

    dragItem.current = null;
    dragOverItem.current = null;
    setList(listCopy);
  };

  const onReshuffle = () => {
    // if (formId) {
    //   setDisabled((pre) => !pre);
    // instituteStudentRearrangeSection({
    //   formId: formId,
    //   bodyParameter: {
    //     shuffle_arr: list,
    //   },
    // })
    //   .then(() => {
    //     instituteStudentFormDetailRefetch();
    //     setDisabled((pre) => !pre);
    //     setResuffle(false);
    //   })
    //   .catch({});
    // }
  };

  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("reshuffle_student_list")}</h6>
              </div>
              <div className={style.modal_container_btn_container}>
                <CreateButton
                  label={t("save_resuffle")}
                  onAction={onReshuffle}
                  customStyle={{
                    margin: "0",
                    padding: "0.3rem 0.5rem",
                  }}
                />
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingBottom: "1rem",
            }}
          >
            {list?.map((student, index) => (
              <div
                key={student?._id}
                draggable
                onDragStart={(e) => onDragEventStart(e, index)}
                onDragEnter={(e) => onDragEventEnter(e, index)}
                onDragEnd={onDropItem}
              >
                <InstituteTheoryClassDetailReshuffleCard student={student} />
              </div>
            ))}
            {classMasterAllStudentLoading ? (
              <Loading />
            ) : (
              classMasterAllStudent?.list?.length === 0 && (
                <EmptyMessage
                  title="No student."
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                />
              )
            )}
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default InstituteTheoryClassDetailReshuffle;
